html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font-weight: inherit;
  font-style: inherit;
  font-variant: inherit;
  font-size: inherit;
  line-height: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

b {
  font-size: 100%;
  font-style: inherit;
  font-variant: inherit;
  font-size: inherit;
  line-height: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button {
  cursor: pointer;
  border-style: none;
}

button::-moz-focus-inner {
  border: none;
}

@keyframes fa-spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

:root {
  --mainnet: #29b6af;
  --inherit: inherit;
  --transparent: transparent;
  --color-network-linea-goerli-default: #61dfff;
  --color-network-linea-goerli-inverse: #fcfcfc;
  --color-network-linea-mainnet-default: #121212;
  --color-network-linea-mainnet-inverse: #fcfcfc;
  --qr-code-white-background: #fff;
}

* {
  box-sizing: border-box;
}

html, body {
  color: var(--color-text-default);
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  overflow: auto;
}

html {
  min-height: 500px;
}

@media screen and (width <= 575px) {
  html:not([data-theme]) {
    background-color: var(--color-background-default);
  }
}

@media (prefers-color-scheme: dark) {
  html:not([data-theme]) {
    color: var(--brand-colors-white-white000);
    background-color: var(--brand-colors-grey-grey900);
  }
}

@media screen and (prefers-color-scheme: dark) and (width <= 575px) {
  html:not([data-theme]) {
    background-color: var(--brand-colors-grey-grey800);
  }
}

.mouse-user-styles button:focus, .mouse-user-styles input:focus, .mouse-user-styles textarea:focus, .mouse-user-styles .unit-input__input, .mouse-user-styles .currency-display__input {
  outline: none;
}

.error {
  color: var(--color-error-default);
  overflow-wrap: anywhere;
  margin-top: 3px;
  margin-bottom: 9px;
}

#app-content {
  flex-direction: column;
  height: 100%;
  display: flex;
  overflow-x: hidden;
}

html[data-theme] #app-content {
  background: var(--color-background-alternative);
}

@media screen and (width <= 575px) {
  html[data-theme] #app-content {
    background-color: var(--color-background-default);
  }
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: var(--color-primary-alternative);
}

input.large-input, textarea.large-input {
  padding: 8px;
}

input.large-input {
  height: 36px;
}

.allcaps {
  text-transform: uppercase;
}

.input-label {
  padding-bottom: 10px;
  font-weight: 400;
  display: inline-block;
}

input.form-control {
  border: 1px solid var(--color-border-default);
  color: 1px solid var(--color-text-default);
  background: none;
  border-radius: 3px;
  width: 100%;
  height: 40px;
  padding-left: 10px;
  font-size: 14px;
}

input.form-control::-webkit-input-placeholder {
  color: var(--color-text-muted);
  font-weight: 100;
}

input.form-control::placeholder {
  color: var(--color-text-muted);
  font-weight: 100;
}

input.form-control:-ms-input-placeholder {
  color: var(--color-text-muted);
  font-weight: 100;
}

input.form-control:-moz-placeholder {
  color: var(--color-text-muted);
  font-weight: 100;
}

input.form-control--error {
  border: 1px solid var(--color-error-default);
}

* {
  font-family: Euclid Circular B, Roboto, Helvetica, Arial, sans-serif;
}

:root {
  --opacity-disabled: .5;
}

