.progressbar {
  counter-reset: step;
  justify-content: space-between;
  margin: 0 auto;
  display: flex;
}

ul.two-steps {
  margin: 0 auto;
}

.progressbar li {
  text-align: center;
  color: var(--color-text-alternative);
  z-index: 2;
  flex-direction: column;
  flex: 1;
  align-items: center;
  font-family: Euclid Circular B, Roboto, Helvetica, Arial, sans-serif;
  font-size: .75rem;
  font-style: normal;
  font-weight: normal;
  line-height: 140%;
  list-style-type: none;
  display: flex;
  position: relative;
}

.progressbar li:before {
  content: counter(step);
  counter-increment: step;
  border: 2px solid var(--color-border-muted);
  text-align: center;
  background-color: var(--color-background-default);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin: 0 auto 10px;
  line-height: 30px;
  display: flex;
}

.progressbar li:after {
  content: "";
  background-color: var(--color-background-alternative);
  width: calc(100% - 34px);
  height: 2px;
  position: absolute;
  top: 15px;
  left: calc(17px - 50%);
}

[dir="rtl"] .progressbar li:after {
  content: "";
  background-color: var(--color-background-alternative);
  width: calc(100% - 34px);
  height: 2px;
  position: absolute;
}

.progressbar li:first-child:after, [dir="rtl"] .progressbar li:first-child:after {
  content: none;
}

.progressbar li.active, [dir="rtl"] .progressbar li.active {
  color: var(--color-primary-default);
}

.progressbar li.active:before, [dir="rtl"] .progressbar li.active:before {
  border-color: var(--color-primary-default);
  z-index: 1;
}

.progressbar li.complete + li:after, [dir="rtl"] .progressbar li.complete + li:after {
  background-color: var(--color-primary-default);
  z-index: -1;
}

.progressbar li.complete:before, [dir="rtl"] .progressbar li.complete:before {
  background-color: var(--color-primary-default);
  color: var(--color-primary-inverse);
}

.progressbar li.two-steps:after {
  content: "";
  background-color: var(--color-background-alternative);
  z-index: -1;
  height: 2px;
  position: absolute;
  top: 15px;
}

[dir="rtl"] .progressbar li.two-steps:after {
  content: "";
  background-color: var(--color-background-alternative);
  z-index: -1;
  width: 180px;
  height: 2px;
  position: absolute;
}

