:root {
  --mainnet: #29b6af;
  --inherit: inherit;
  --transparent: transparent;
  --color-network-linea-goerli-default: #61dfff;
  --color-network-linea-goerli-inverse: #fcfcfc;
  --color-network-linea-mainnet-default: #121212;
  --color-network-linea-mainnet-inverse: #fcfcfc;
  --qr-code-white-background: #fff;
}

* {
  box-sizing: border-box;
}

html,
body {
  color: var(--color-text-default);
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  overflow: auto;
}

html {
  min-height: 500px;
  /*
    Until we get the user's preference from React state,
    assume the user is in dark mode, because the default
    theme is system preference
  */
}
@media screen and (max-width: calc(576px - 1px)) {
  html:not([data-theme]) {
    background-color: var(--color-background-default);
  }
}
@media (prefers-color-scheme: dark) {
  html:not([data-theme]) {
    color: var(--brand-colors-white-white000);
    background-color: var(--brand-colors-grey-grey900);
  }
}
@media screen and (prefers-color-scheme: dark) and (max-width: calc(576px - 1px)) {
  html:not([data-theme]) {
    background-color: var(--brand-colors-grey-grey800);
  }
}

.mouse-user-styles button:focus,
.mouse-user-styles input:focus,
.mouse-user-styles textarea:focus,
.mouse-user-styles .unit-input__input,
.mouse-user-styles .currency-display__input {
  outline: none;
}

/*
  This error class is used in the following files still:
  /ui/pages/create-account/import-account/json.js
  /ui/pages/create-account/import-account/private-key.js
*/
.error {
  color: var(--color-error-default);
  margin-top: 3px;
  margin-bottom: 9px;
  overflow-wrap: anywhere;
}

/* stylelint-disable */
#app-content {
  overflow-x: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
}
html[data-theme] #app-content {
  background: var(--color-background-alternative);
}
@media screen and (max-width: calc(576px - 1px)) {
  html[data-theme] #app-content {
    background-color: var(--color-background-default);
  }
}

/* stylelint-enable */
a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  color: var(--color-primary-alternative);
