.progressbar {
  counter-reset: step;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}

ul.two-steps {
  margin: 0 auto;
}

.progressbar li {
  font-size: 0.75rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  font-style: normal;
  font-weight: normal;
  list-style-type: none;
  position: relative;
  text-align: center;
  color: var(--color-text-alternative);
  z-index: 2;
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
}

.progressbar li::before {
  width: 30px;
  height: 30px;
  content: counter(step);
  counter-increment: step;
  line-height: 30px;
  border: 2px solid var(--color-border-muted);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0 auto 10px auto;
  border-radius: 50%;
  background-color: var(--color-background-default);
}

.progressbar li::after {
  width: calc(100% - 34px);
  height: 2px;
  content: "";
  position: absolute;
  background-color: var(--color-background-alternative);
  top: 15px;
  left: calc(-50% + 17px);
}
[dir=rtl] .progressbar li::after {
  width: calc(100% - 34px);
  height: 2px;
  content: "";
  position: absolute;
  background-color: var(--color-background-alternative);
}

.progressbar li:first-child::after {
  content: none;
}
[dir=rtl] .progressbar li:first-child::after {
  content: none;
}

.progressbar li.active {
  color: var(--color-primary-default);
}
[dir=rtl] .progressbar li.active {
  color: var(--color-primary-default);
}

.progressbar li.active::before {
  border-color: var(--color-primary-default);
  z-index: 1;
}
[dir=rtl] .progressbar li.active::before {
  border-color: var(--color-primary-default);
  z-index: 1;
}

.progressbar li.complete + li::after {
  background-color: var(--color-primary-default);
  z-index: -1;
}
[dir=rtl] .progressbar li.complete + li::after {
  background-color: var(--color-primary-default);
  z-index: -1;
}

.progressbar li.complete::before {
  background-color: var(--color-primary-default);
  color: var(--color-primary-inverse);
}
[dir=rtl] .progressbar li.complete::before {
  background-color: var(--color-primary-default);
  color: var(--color-primary-inverse);
}

.progressbar li.two-steps::after {
  height: 2px;
  content: "";
  position: absolute;
  background-color: var(--color-background-alternative);
  top: 15px;
  z-index: -1;
}
[dir=rtl] .progressbar li.two-steps::after {
  width: 180px;
  height: 2px;
  content: "";
  position: absolute;
  background-color: var(--color-background-alternative);
  z-index: -1;
}

