.definition-list__term {
  display: flex;
  align-items: center;
}
.definition-list__term .definition-list__tooltip-wrapper {
  display: flex !important;
  align-items: center;
}

.dialog {
  font-size: 0.75rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  font-style: normal;
  font-weight: normal;
  padding: 1rem;
  box-sizing: border-box;
  border-radius: 8px;
}
.dialog--message {
  color: var(--color-text-default);
  background-color: var(--color-info-muted);
  border: 1px solid var(--color-info-default);
}
.dialog--error {
  color: 1px solid var(--color-text-default);
  background-color: var(--color-error-muted);
  border: 1px solid var(--color-error-default);
}
.dialog--warning {
  color: 1px solid var(--color-text-default);
  background-color: var(--color-warning-muted);
  border: 1px solid var(--color-warning-default);
}

.dropdown {
  position: relative;
  display: inline-block;
  height: 36px;
}
.dropdown__select {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  font-size: 0.875rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  font-style: normal;
  font-weight: normal;
  color: var(--color-text-default);
  border: 1px solid var(--color-border-default);
  border-radius: 4px;
  background-color: var(--color-background-default);
  padding: 8px 40px 8px 16px;
  width: 100%;
}
[dir=rtl] .dropdown__select {
  padding: 8px 16px 8px 40px;
}
.dropdown__icon-caret-down {
  position: absolute;
  right: 16px;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
}
[dir=rtl] .dropdown__icon-caret-down {
  left: 16px;
}

.error-message {
  font-size: 0.75rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  font-style: normal;
  font-weight: normal;
  min-height: 32px;
  border: 1px solid var(--color-error-default);
  color: var(--color-text-default);
  background-color: var(--color-error-muted);
  border-radius: 8px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 10px;
}
.error-message__text {
  overflow: auto;
}

.icon-border {
  border-radius: 50%;
  border: 1px solid var(--color-border-muted);
  background: var(--color-background-alternative);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.icon-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: unset;
  text-align: center;
  width: 60px;
  font-size: 0.75rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  cursor: pointer;
  color: var(--color-primary-default);
}
.icon-button__circle {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 36px;
  width: 36px;
  background: var(--color-primary-default);
  border-radius: 18px;
  margin-top: 6px;
  margin-bottom: 5px;
  margin-inline: auto;
}
.icon-button--disabled {
  opacity: 0.3;
  cursor: auto;
}
.icon-button__label {
  width: 60px;
}

.icon-with-fallback__fallback {
  color: var(--color-icon-alternative);
}

.info-icon {
  margin: 0 4px;
}
.info-icon--success {
  fill: var(--color-success-default);
}
.info-icon--info {
  fill: var(--color-info-default);
}
.info-icon--warning {
  fill: var(--color-warning-default);
}
.info-icon--danger {
  fill: var(--color-error-default);
}

.preloader__icon {
  animation-name: spin;
  animation-duration: 500ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.identicon {
  /*rtl:ignore*/
  direction: ltr;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.identicon__address-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  border-style: solid;
  border-radius: 50%;
  border-width: 2px;
  border-color: var(--color-primary-default);
}
.identicon__image-border {
  border: 1px solid var(--color-border-default);
  background: var(--color-background-default);
}

.info-tooltip svg {
  height: 12px;
  width: 12px;
}
.info-tooltip__tooltip-container svg {
  position: static;
}

.tippy-tooltip.tippy-tooltip-info-theme {
  max-width: 200px;
  padding: 16px;
  padding-bottom: 16px;
}

.list-item {
  width: 100%;
  min-height: 86px;
  margin: 0;
  background: var(--color-background-default);
  padding: 24px 16px;
  font-size: 1rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  font-style: normal;
  font-weight: normal;
  border-bottom: 1px solid var(--color-border-muted);
  color: var(--color-text-default);
