.ens-input__wrapper {
  flex: 1 1 auto;
  width: 0;
  align-items: center;
  background: var(--color-background-default);
  border-radius: 0.5rem;
  padding: 0.75rem 0.5rem;
  border: 1px solid var(--color-border-muted);
  transition: border-color 150ms ease-in-out;
}
.ens-input__wrapper:focus-within {
  border-color: var(--color-border-default);
}
.ens-input__wrapper__status-icon {
  margin: 4px 8px 4px 4px;
}
.ens-input__wrapper__input {
  font-size: 0.875rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  font-style: normal;
  font-weight: normal;
  flex: 1 1 auto;
  width: 0;
  border: 0;
  outline: none;
  color: var(--color-text-default);
  background-color: var(--color-background-default);
}
.ens-input__wrapper__input::placeholder {
  color: var(--color-text-muted);
}
.ens-input__wrapper__action-icon-button {
  background: none;
  border: none;
  padding: 0 8px;
  height: 24px;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ens-input__wrapper--valid {
  border-color: var(--color-primary-default);
}
.ens-input__wrapper--valid .ens-input__wrapper__input {
  font-size: 0.75rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  font-style: normal;
  font-weight: normal;
  color: var(--color-primary-default);
}
.ens-input__selected-input__title {
  font-size: 0.875rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  font-style: normal;
  font-weight: normal;
  word-wrap: break-word;
  white-space: inherit !important;
}
.ens-input__selected-input__subtitle {
  font-size: 0.75rem;
  font-family: "Euclid Circular B", Roboto, Helvetica, Arial, sans-serif;
  line-height: 140%;
  font-style: normal;
  font-weight: normal;
  color: var(--color-text-default);
